.navigation {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;

  .logo_container {
    height: 100%;
    width: 70px;
    padding: 25px;
  }

  .nav_links_container {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .nav_link {
      width: 150px;
      padding: 10px 15px;
      cursor: pointer;
    }
  }

}

hr {
  border: 1px dashed #bbb;
  opacity: 0.5;
}  
